#lister {
    width: 100vw;
    height: 100px;
    background: url('../assets/about-header.png') no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;
}

#lister::before {
    content: '';
    width: 100vw;
    height: 100px;
    position: absolute;
    z-index: 2;
    top: 80px;
    background: url('../assets/about-header-overlay.png') repeat-x;
}

#lister .ctn {
    height: 100px;
    display: flex;
    position: absolute;
    left: 250px;
}

#lister .ctn a {
    width: 200px;
    height: 100px;
    background: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    font-weight: bold;
    color: white;
    transition: .3s;
    text-shadow: 2px 2px 1px rgba(0, 0, 0, .3);
}

#lister .ctn a:hover {
    background: rgba(0, 0, 0, .5)
}

#lister .ctn a:hover:after {
    content: '';
    width: 125px;
    height: 5px;
    background: white;
    position: absolute;
    top: 0;
}

#lister .btn {
    height: 100px;
    display: flex;
    position: absolute;
    right: 20px;
}

#lister .btn a {
    width: 30px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 15px 0 15px;
}

#lister .btn a svg {
    color: rgba(255, 255, 255, .7);
    font-size: 30px;
    transition: .5s;
}

#lister .btn a svg:hover {
    color: rgba(255, 255, 255, 1);
}