#loader {
    width: 100vw;
    height: 100vh;
    background: #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

#loader.vibs {
    animation: fadeOut 1s forwards;
}

#loader span {
    font-size: 24px;
    font-weight: bold;
    color: #1d1e22;
    position: absolute;
    top: 30vw;
}

#loader h1 {
    font-size: 48px;
    font-weight: bold;
    color: #1d1e22;
    opacity: .5;
    position: absolute;
    top: 10vw;
}

#b0 {
    animation: b0 4s infinite ease;
    transform-origin: 250px 250px;
}

#b1 {
    animation: b1 4s infinite ease;
}

#b2 {
    animation: b2 4s infinite ease;
}

#b3 {
    animation: b3 4s infinite ease;
}

#b4 {
    animation: b4 4s infinite ease;
}

@keyframes b0 {
    0% {
        transform: rotate(0);
    }

    25% {
        transform: rotate(-90deg);
    }

    50% {
        transform: rotate(-180deg);
    }

    75% {
        transform: rotate(-270deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}

@keyframes b1 {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(0, 355px);
    }

    50% {
        transform: translate(355px, 355px);
    }

    75% {
        transform: translate(355px, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes b2 {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(-355px, 0);
    }

    50% {
        transform: translate(-355px, 355px);
    }

    75% {
        transform: translate(0, 355px);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes b3 {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(0, -355px);
    }

    50% {
        transform: translate(-355px, -355px);
    }

    75% {
        transform: translate(-355px, 0);
    }

    100% {
        transform: translate(0, 0);
    }
}

@keyframes b4 {
    0% {
        transform: translate(0, 0);
    }

    25% {
        transform: translate(355px, 0);
    }

    50% {
        transform: translate(355px, -355px);
    }

    75% {
        transform: translate(0, -355px);
    }

    100% {
        transform: translate(0, 0);
    }
}


.loader,
.loader::before,
.loader::after {
    border-width: 2px;
    border-style: solid;
    border-radius: 10px;
    animation: rotate 5s linear infinite;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 40px;
    height: 40px;
    border-color: #5a4ff3;
}

.loader::before,
.loader::after {
    position: absolute;
    content: "";
}

.loader::before {
    border-color: #35a2d2;
    width: 110%;
    height: 110%;
    animation-delay: .5s
}

.loader::after {
    border-color: #9c40fc;
    width: 120%;
    height: 120%;
    animation-delay: .10s;
}

@keyframes rotate {
    to {
        transform: rotate(360deg)
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
        visibility: hidden;
    }
}